import '../src/styles/main.scss';
import Swiper from 'swiper';
import 'swiper/css';

const gallerySwiper = new Swiper('.sectionGallery__swiper', {
    effect: "coverflow",
    grabCursor: true,
    centeredSlides: false,
    speed: 600,
    slidesPerView: "auto",
    spaceBetween: 30,
    navigation: {
        nextEl: '.sectionGallery__button--next',
        prevEl: '.sectionGallery__button--prev',
    },
    breakpoints: {
        768: { slidesPerView: 1 },
        1024: { slidesPerView: 2 }
    },
    on: {
        init: function() {
            updateNavigationButtons(this);
        },
        slideChange: function() {
            updateNavigationButtons(this);
        }
    }
});

function updateNavigationButtons(swiper) {
    const prevBtn = document.querySelector('.sectionGallery__button--prev');
    const nextBtn = document.querySelector('.sectionGallery__button--next');
    
    prevBtn.classList.toggle('sectionGallery__button--hidden', swiper.isBeginning);
    nextBtn.classList.toggle('sectionGallery__button--hidden', swiper.isEnd);
}